"use client"

import { useEffect, useState } from "react"
import { cn } from "@/lib/utils"

const navItems = [
  { name: "About", href: "#about" },
  { name: "Experience", href: "#experience" },
  { name: "Projects", href: "#projects" },
  { name: "Education", href: "#education" },
]

export function Nav() {
  const [activeSection, setActiveSection] = useState("")

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setActiveSection(entry.target.id)
          }
        })
      },
      { threshold: 0.5 },
    )

    navItems.forEach((item) => {
      const element = document.getElementById(item.href.slice(1))
      if (element) observer.observe(element)
    })

    return () => observer.disconnect()
  }, [])

  return (
    <nav className="hidden lg:block mt-16">
      <ul className="flex flex-col gap-4">
        {navItems.map((item) => (
          <li key={item.name}>
            <a
              href={item.href}
              className={cn(
                "group flex items-center py-3 transition-all",
                activeSection === item.href.slice(1) ? "text-primary" : "text-muted-foreground hover:text-foreground",
              )}
            >
              <span
                className={cn(
                  "mr-4 h-px transition-all group-hover:w-16 group-hover:bg-foreground",
                  activeSection === item.href.slice(1) ? "w-16 bg-primary" : "w-8 bg-muted-foreground",
                )}
              />
              <span className="text-xs font-bold uppercase tracking-widest">{item.name}</span>
            </a>
          </li>
        ))}
      </ul>
    </nav>
  )
}
